p,
h2,
h3 {
  text-align: left;
  color: #fff;
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
}

.bgc {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: no-repeat url(./img/mbgc.png);
  background-size: cover;
  z-index: -1;
}

.girls {
  position: absolute;
  top: 0;
  right: 0;
  width: 207px;
  height: 477px;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 17px 0 0 24px;
}

.logoImg {
  width: 36px;
  height: 61px;
  margin-bottom: 55px;
}

.logo {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.desc {
  font-weight: 400;
  font-size: 9px;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 120%;
  margin-bottom: 20px;
}

.content {
  display: flex;
  position: absolute;
  justify-content: space-between;
  bottom: 40px;
  left: 20px;
  width: 200px;
}

.text {
  font-weight: 500;
  font-size: 11px;
  line-height: 150%;
}

.bsize,
.bsoc {
  display: none;
}

.lsize,
.lsoc {
  display: block;
}

@media screen and (min-width: 700px) {
  .bgc {
    background: no-repeat url(./img/bgc.png);
    background-size: cover;
  }

  .girls {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 703px;
    height: 100vh;
  }

  .bsize {
    display: block;
  }
  .lsize {
    display: none;
  }
}

@media screen and (min-width: 1000px) {
  .girls {
    right: 15%;
  }

  .header {
    padding: 42px 0 0 64px;
  }

  .logoImg {
    width: 55px;
    height: 92px;
    margin-bottom: 110px;
  }

  .logo {
    font-size: 44px;
  }

  .desc {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 3px;
  }

  .content {
    right: 35px;
    bottom: 35px;
    left: auto;
    width: 250px;
  }

  .text {
    font-weight: 500;
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .girls {
    right: 20%;
  }

  .bsoc {
    display: block;
    position: absolute;
    top: 55px;
    right: 80px;
  }

  .lsoc {
    display: none;
  }

  .logo {
    font-size: 64px;
    line-height: 74px;
    margin-bottom: 30px;
  }

  .desc {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 3px;
  }
}

@media screen and (min-height: 780px) {
  .girls {
    right: 0;
    min-width: 100%;
  }
}
